module.exports = ({ el, app, components }) => () => {

    const $whatIsGravatar = el('a', {
        text: 'What is Gravatar?',
        href: 'https://en.gravatar.com/support/what-is-gravatar/',
        target: '_blank'
    });

    const $title = el('span', {
        textContent: 'Import images from Gravatar'
    }).append($whatIsGravatar);

    return components.elements.modal('gravatar', app.gravatar(), $title);
    
};
