module.exports = ({ el, services, util }) => () => {

    const $chooseImages = el('a', 'choose-images', {
        textContent: 'Choose images'
    }).addEventListener('click', e => {
        e.preventDefault();
        $fileInput.click();
    });

    const $fileInput = el('input', {
        type: 'file',
        multiple: true,
        accept: 'image/*'
    }).addEventListener('change', () => {
        util.getFiles($fileInput).forEach(async file => {
            const tag = await services.tags.dataSources.imageFile(file);
            services.tags.insertTag(tag);
        });
    });

    return el('span').append($chooseImages, $fileInput);

};
