const buildRole = require('./build-role');

module.exports = ({ stores, services }) => (name = '') => {
    
    name = name.trim();
    
    const pattern = new RegExp(`^${name}$`, 'i');
    const existingRole = stores.roles
        .getArray()
        .find(role => pattern.test(role.name));

    return existingRole ? existingRole.id : services.roles.insertRole({ name });
};
