module.exports = ({ services, settings }) => async () => {
    const { freetext, fallback } = settings.gravatar.getState();

    const emails = freetext
        .split(/[\r\n,;]+/g)
        .map(s => s.trim())
        .filter(s => s);

    services.gravatar.status.working();

    const insert = async email => {
        const tag = await services.tags.dataSources.gravatar(email, fallback);
        return services.tags.insertTag(tag);
    };

    try {
        await Promise.all(emails.map(insert));
        services.gravatar.status.ready();
        services.settings.clearModal();
        return true;
    } catch (err) {
        // TODO: log to sentry
        // console.error(err);
        const errorMessage = 'An error occurred. Please check your connection and try again.';
        services.gravatar.status.error(errorMessage);
        return false;
    }
};
