module.exports = ({ el, settings }) => fallback => {
    
    const $fallback = el('img', 'fallback', {
        title: fallback,
        src: `img/gravatar-fallbacks/${fallback}.png`
    }).addEventListener('click', () => {
        settings.gravatar.setState({ fallback });
    });

    settings.gravatar.onChange('fallback', ({ fallback: selectedFallback }) => {
        $fallback.classList.toggle('selected', fallback === selectedFallback);
    }).invoke();

    return $fallback;
};
