const storeNames = ['settings', 'roles', 'tags', 'tagInstances'];
const stateStore = storeName => ({ util, state }) => util.stateStore(state[storeName] = {});

module.exports = storeNames.reduce((acc, name) => {
    return Object.assign(acc, { [name]: stateStore(name) });
}, { __modulename: 'stores' });
