module.exports = ({ core, services, util }) => tagData => {

    const pipeline = [
        core.tags.dataTransforms.defaults,
        core.tags.dataTransforms.expression,
        core.tags.dataTransforms.tagName,
        services.tags.dataTransforms.roleName,
        services.tags.dataTransforms.imageBlob
    ];

    return util.pipeAsync(pipeline, tagData);
    
};
