module.exports = ({ util }) => async tagData => {

    const { imageBlob } = tagData;
        
    if (!imageBlob) return tagData;
    if (!imageBlob.type.startsWith('image/')) {
        // TODO: find out why this happens in tests.
        return tagData;
    }
    const image = await util.readAsDataUrl(imageBlob);
    return { ...tagData, image };
    
};
