module.exports = ({ el, styles, stores }) => () => {

    const $styles = el('div').append(
        styles.styles.tagShape(),
        styles.styles.tagSize(),
        styles.styles.tagSpacing(),
        styles.styles.tagOutline()
    );

    stores.roles.onInsert(roleId => $styles.append(styles.styles.role(roleId)));
    stores.tags.onInsert(tagId => $styles.append(styles.styles.tagImage(tagId)));

    return $styles;

};
