module.exports = ({ el, components, settings, config }) => () => {

    const $$options = Object.entries(config.options.sort).map(([value, textContent]) => {
        return el('option', { value, textContent });
    });

    const $keepSorted = el('select')
        .append(...$$options)
        .addEventListener('change', () => {
            settings.options.setState({ sort: $keepSorted.value });
        });

    settings.options.onChange('sort', ({ sort }) => {
        $keepSorted.value = sort;
    }).invoke();

    return components.elements.label('Keep sorted by', $keepSorted);
    
};
