module.exports = ({ components, services, stores }) => instanceId => {

    const $role = components.elements.editableSpan(e => {
        const name = e.target.textContent.trim();
        services.tags.changeInstanceRole(instanceId, name);
    });

    $role.className = 'role-badge';

    stores.tagInstances.onChange(instanceId, 'roleName', ({ roleName }) => {
        $role.textContent = roleName;
    }).invoke();

    return $role;

};
