const Sentry = require('@sentry/browser');
const initialise = require('./initialise');
const config = require('./config');

module.exports = window => {    
    const dsn = window.location.hostname === 'localhost' ? null : config.analytics.sentry.dsn;
    Sentry.init({ dsn });
    
    const { services, components, context } = initialise({ window });
    
    window.agileavatars = context;
    document.title = config.app.name;
    document.head.append(components.analytics.google(window));
    document.body.append(components.app());
    services.settings.setModal('welcome');
};
