module.exports = ({ el, optionsBar, stores, util, config }) => () => {
    
    const $$optionGroups = util.parseLayout(config.options.layout).map(group => {
        const $$options = group.map(name => optionsBar.options[name]());
        return el('div', 'option-group').append(...$$options);
    });

    const $optionsBar = el('div', 'options-bar').append(...$$optionGroups);
    const $container = el('div', 'options-bar-container visible-false').append($optionsBar);

    stores.tags.onInsert(() => {
        $container.className = 'options-bar-container visible-true';
    });

    return $container;
    
};
