module.exports = ({ el, services, settings }) => () => {

    const { status } = services.gravatar;

    const $importButton = el('button', { textContent: 'Import' })
        .addEventListener('click', () => services.tags.insertGravatarBatch());
    
    const $loadingSpinner = el('div');
    const $errorMessage = el('div');

    const $dismissErrorMessage = el('a', { textContent: 'Dismiss' })
        .addEventListener('click', status.ready);

    const $actions = el('div', 'actions')
        .append($importButton, $loadingSpinner, $errorMessage);

    settings.gravatar.onChange('status', () => {
        $importButton.className = `import visible-${status.isReady()}`;
        $loadingSpinner.className = `lds-dual-ring visible-${status.isWorking()}`;
        $errorMessage.className = `error-message visible-${status.isError()}`;
    }).invoke();

    settings.gravatar.onChange('errorMessage', ({ errorMessage }) => {
        $errorMessage.textContent = errorMessage;
        $errorMessage.append(el('br'), $dismissErrorMessage);
    }).invoke();

    return $actions;

};
