module.exports = ({ el, services, settings }) => (name, content, title) => {

    const $dismiss = el('span', { className: 'dismiss' })
        .addEventListener('click', services.settings.clearModal);

    const $title = el('div', { className: `modal-title visible-${Boolean(title)}` })
        .append(title, $dismiss);
    
    const $content = el('div', { className: 'modal-content' })
        .append(content);

    const $prompt = el('div', { className: 'modal-prompt' })
        .append($title, $content)
        .addEventListener('click', e => e.stopPropagation());
        // without this, clicking on prompt will dismiss it.

    const $overlay = el('div')
        .append($prompt)
        .addEventListener('click', services.settings.clearModal);

    settings.app.onChange('modal', ({ modal }) => {
        $overlay.className = `modal-overlay visible-${modal === name}`;
    }).invoke();
    
    return $overlay;

};
