module.exports = ({ el, settings }) => () => {
    
    const $showOutline = el('input', { type: 'checkbox' })
        .addEventListener('change', () => {
            settings.options.setState({ outline: $showOutline.checked });
        });

    const $container = el('span').append($showOutline, 'Show outline');

    settings.options.onChange('outline', ({ outline }) => {
        $showOutline.checked = outline;
    }).invoke();

    return $container;
    
};
