const splitAt = (x, index) => [x.slice(0, index), x.slice(index + 1)];

module.exports = () => expression => {

    const indexOfAt = expression.indexOf('@');
    const [name] = expression.substr(0, indexOfAt).split('+');
    const lastIndexOfPlus = expression.lastIndexOf('+');
    const hasRole = lastIndexOfPlus > indexOfAt;        
    const [email, roleName] = hasRole ? splitAt(expression, lastIndexOfPlus) : [expression];
    return { name, email, roleName };

};
