const settingsNames = ['app', 'options', 'gravatar'];

module.exports = ({ config, stores }) => {
    return settingsNames.reduce((acc, name) => {
        const defaults = config.defaultSettings[name];
        const id = stores.settings.insert(defaults);
        return Object.assign(acc, { [name]: stores.settings.manage(id) });
    }, {});
};
