module.exports = ({ el, settings, components }) => () => {

    const $freetext = el('textarea', 'freetext')
        .addEventListener('blur', e => {
            const freetext = e.target.value;
            settings.gravatar.setState({ freetext });
        });

    settings.gravatar.onChange('freetext', ({ freetext }) => {
        $freetext.value = freetext;
    }).invoke();

    settings.gravatar.onChange('status', ({ status }) => {
        $freetext.disabled = status === 'working';
    }).invoke();

    const labelText = `
        Enter email addresses to import images from Gravatar.<br />
        Alternatively, enter names to use one of the generated image styles below.`;

    return components.elements.label(labelText, $freetext);
    
};
