module.exports = ({ core }) => expression => {

    if (!expression) return {};

    const isEmail = expression.includes('@');
    if (isEmail) return core.tags.parseEmailExpression(expression);

    const [name, roleName] = expression
        .split('/')
        .pop()
        .match(/^(\d+)?(.+)/)[2]
        .split('.')[0]
        .split('+')
        .map(s => s.trim());
        
    return { name, roleName };

};

/*

- Leading numbers are stripped, allowing them to be used for natural ordering.
  This enables the user to drag and drop a selection of files and have them rendered in their preferred order.
- Any file extensions are stripped from the first dot.
- The remainder of the expression is split on + to allow an optional role name.

*/
