const Picker = require('vanilla-picker').default || require('vanilla-picker');

module.exports = ({ el, stores }) => roleId => {

    const role = stores.roles.getState(roleId);

    const $colourPicker = el('div', 'colour-picker');

    new Picker({
        parent: $colourPicker,
        color: role.colour,
        onChange: e => stores.roles.setState(roleId, { colour: e.hex })
    });

    return $colourPicker;

};
