module.exports = ({ components, services, stores }) => instanceId => {

    const $name = components.elements.editableSpan(e => {
        const name = e.target.textContent.trim();
        const { tagId } = stores.tagInstances.getState(instanceId);
        services.tags.changeTagName(tagId, name);
    });

    $name.className = 'tag-name';

    stores.tagInstances.onChange(instanceId, 'name', ({ name }) => {
        $name.textContent = name;
    }).invoke();

    return $name;

};
