module.exports = ({ el, tag, services, stores }) => instanceId => {

    const $tag = el('div').append(tag.layout(instanceId));

    stores.tagInstances.onChange(instanceId, 'roleId', ({ tagId, roleId, mode }) => {
        const isNilRole = services.roles.isNilRole(roleId);
        $tag.className = `tag tag${tagId} role${roleId} nil-role-${isNilRole} ${mode}`;
    }).invoke();

    return $tag;

};
