module.exports = ({ services, stores }) => async tagData => {

    const tag = await services.tags.buildTag(tagData);

    return stores.tags.insert(tag, tagId => {
        stores.tags.onChange(tagId, 'name', services.tags.propagateTagName(tagId)).invoke();
        stores.tags.onChange(tagId, 'roleId', services.tags.propagateRoleId(tagId)).invoke();
        services.tags.adjustInstanceCounts()();
    });
    
};
