const debounce = require('lodash/debounce');
const pick = require('lodash/pick');

module.exports = ({ core, config, settings, stores, services }) => {
    const adjustInstanceCounts = () => {
        const { insertInstance, removeInstance } = services.tags;

        const tags = stores.tags.getArray();
        const modeCounts = pick(settings.options.getState(), config.options.modes);
        const plans = core.tags.planInstanceCountAdjustment(tags, modeCounts);

        plans.forEach(plan => {
            plan = { insert: [], remove: [], ...plan };
            plan.insert.forEach(insertInstance);
            plan.remove.forEach(removeInstance);
        });
    };

    return () => {
        return debounce(adjustInstanceCounts, config.debounce.adjustInstanceCounts);
    };
};
