module.exports = ({ el, app, components }) => () => {

    const { roleShortcut, naming, images, laminating, multiples, badges } = app.tips;
    const sequence = [roleShortcut, naming, images, laminating, multiples, badges];

    const $tips = el('div', 'tips');

    sequence.forEach(render => {
        const $tip = render();
        $tip.className = 'tip';
        const $heading = el('div', 'heading', { textContent: $tip.title });        
        $tip.prepend($heading);
        $tips.append($tip);
    });

    return components.elements.modal('tips', $tips, 'Tips & tricks');
    
};
