module.exports = ({ el, settings }) => shapeName => {
    
    const $shape = el('span', `shape-option ${shapeName}`, {
        title: `Change shape to ${shapeName}`
    }).addEventListener('click', () => {
        settings.options.setState({ shape: shapeName });
    });

    settings.options.onChange('shape', ({ shape: selectedShape }) => {
        $shape.classList.toggle('selected', shapeName === selectedShape);
    }).invoke();

    return $shape;
    
};
