const compose = require('module-composer');
const merge = require('lodash/merge');
const lib = require('./lib');
const src = {
    config: require('./config'),
    io: require('./io')
};

module.exports = ({ window, ...args }) => {
    const config = merge({}, src.config, args.config);
    const io = merge({}, src.io({ window }), args.io);

    const state = {};
    const util = compose(lib.util, { io, window });    
    const stores = compose(lib.stores, { util, state });
    const settings = lib.settings({ config, stores });
    const core = compose(lib.core, { config });
    const services = compose(lib.services, { util, io, config, stores, settings, core });
    const { el } = util;
    const components = compose.collapse(lib.components, { el, util, io, config, stores, settings, services });
    
    services.system.initialise();
    
    const context = {
        state,
        io,
        util,
        config,
        stores,
        settings,
        services,
        components
    };

    return { context, ...context };
};
