module.exports = ({ components, services, stores }) => roleId => {

    const $roleBadge = components.elements.editableSpan(e => {
        const name = e.target.textContent;
        services.roles.changeRoleName(roleId, name);
    });
    
    $roleBadge.className = `role-badge role${roleId}`;

    stores.roles.onChange(roleId, 'name', ({ name }) => {
        $roleBadge.textContent = name;
    }).invoke();

    return $roleBadge;

};
