module.exports = ({ el, services }) => dropEventData => {

    const drop = e1 => {
        const e = dropEventData || e1;
        Array.from(e.dataTransfer.files).forEach(async file => {
            const tag = await services.tags.dataSources.imageFile(file);
            services.tags.insertTag(tag);
        });
        return silence(e);
    };
    
    return el('div', { className: 'dropzone' })
        .addEventListener('dragenter', silence, false)
        .addEventListener('dragover', silence, false)
        .addEventListener('drop', drop, false);
        
};

const silence = e => {
    e.stopPropagation();
    e.preventDefault();
    return false;
};

